<template>
  <div id="app">
    <vs-card-group v-if="news()">
      <Card v-for="card in newsList" :key="card" :c="card" v-show="checkImage(card.image_url)"></Card>
    </vs-card-group>
    <Table></Table>
    <vs-button @click="news()">update</vs-button>
    <!--
    <Subscribe></Subscribe>
    <Footer :Cards="[]"></Footer>-->
  </div>
</template>

<script>
import Vue from 'vue'
import Vuesax from 'vuesax'

import 'vuesax/dist/vuesax.css'
//import Navbar from './components/Navbar'
import Card from "./components/Card";
import Table from "./components/Table";

Vue.use(Vuesax, {
  colors: {
    primary:'#5b3cc4',
    success:'rgb(23, 201, 100)',
    danger:'rgb(242, 19, 93)',
    warning:'rgb(255, 130, 0)',
    dark:'rgb(36, 33, 69)'
  }
})

export default {
  name: 'App',
  components: {
    Card,
    Table
  },
  methods: {
    checkImage: function (url) {
      const img = new Image();
      img.src = url;

      if (img.complete) {
        return true
      } else {
        img.onload = () => {
          return true
        };
        img.onerror = () => {
          return false
        };
      }
    },
    news: async function () {
      let response = await fetch('http://localhost:5000/news');
      if (response.ok) {
        let json = await response.json();
        this.newsList = json
        return true
      } else {
        alert("HTTP-Error: " + response.status);
        return false
      }
    }
  },
  data: () => ({
    newsList: []
  })
}
</script>

<style>
#app {
  font-family: monospace;
}
</style>
